import React, {useState, useRef} from 'react';
import axios from "axios";
import '../App.css';
import { doc, setDoc, addDoc, collection } from "firebase/firestore";
import db from '../firebase'
import { NaverMap, RenderAfterNavermapsLoaded , Marker} from 'react-naver-maps';

function NaverMapComponent() {
     const id = 'u4q85ykbgr';
      return ( 
      <NaverMap mapDivId={"react-naver-map"}
       style={{ width: '60%', height: '60vh' }} 
       defaultCenter={{ lat: 37.5680443, lng: 126.8266372 }} defaultZoom={18} 
       /> 
       );
     }


const Main = () =>{ 
    return(
        <div>
            !
            
            <div>
                <RenderAfterNavermapsLoaded ncpClientId='u4q85ykbgr'
                error={<p>Maps Load Error</p>} 
                loading={<p>Maps Loading...</p>}
                 >  
                      <NaverMapComponent /> 
                      <Marker
                       key='0'
                        position={[ 37.5680443,126.8266372]}
                           onClick={() => { }} 
                           />

                      </RenderAfterNavermapsLoaded>

    </div>
        </div>
    )
}
export default Main;