import React, {useEffect, useState} from 'react';
import axios from "axios";
import '../App.css';
import { doc, setDoc, addDoc, collection, onSnapshot, updateDoc } from "firebase/firestore";
import db from '../firebase'

import { firestore } from "../firebase.js";


const Chat = () =>{
    const [txtChatUserName, setTxtChatUserName] = useState('');
    const [txtChatText, setTxtChatText] = useState('');

    const chatDatas = firestore.collection("chatDatas");
    

    const onChatTextChange = (e) =>{
        setTxtChatText(e.target.value);
    }
    const onUserNameChange = (e) =>{
        setTxtChatUserName(e.target.value);
    }

    const onReset = () => {
        setTxtChatText('');
    }

    const upload = async () =>{
        // await setDoc(doc(db, "testData", "LA"), {
        // name: "Los Angeles",
        // state: "CA",
        // country: "USA"
        let now = new Date();
        await addDoc(collection(db, 'chatDatas'), {
            name: txtChatUserName
            , text: txtChatText
            , ymd : now.getFullYear() + '-' + filledDate(now.getUTCMonth()+1) + '-' + now.getUTCDate()
            , time : filledDate(now.getHours()) + ':' + filledDate(now.getMinutes()) + ':' + filledDate(now.getSeconds())
      }
      );
      
      reloadChat();
      setTxtChatText('')
    }
    function filledDate(num){
        return ('0' + num).slice(-2);
    }
    
    let [userName, userNameChange] = useState([]);
    let [userChat, userChatChange] = useState([]);
    let i = 0;

    useEffect( () => {
        reloadChat();
    }, []);

    function reloadChat(){
        chatDatas.orderBy('ymd').orderBy('time').get().then((docs)=>{
            let temp = [];
           docs.forEach((doc) =>{
               temp.push({key:i++,name:doc.data().name, text:doc.data().text, ymd:doc.data().ymd})
               console.log('ymd' + doc.data().ymd  + ' / time ' + doc.data().time)
           })
           userNameChange(temp);
         });
    }

      function AddChat(uName, uChat){
          userNameChange(userName.concat(uName))
          userChatChange(userChat.concat(uChat))
      }

      let [befDate, SetBefDate] = useState();
    return(
        <div >
            <div>  :  
            <input onChange={onUserNameChange} value={txtChatUserName} />
            </div>
            <div>  : 
                <input onChange={onChatTextChange} value={txtChatText}/>
                </div>
            <button onClick={onReset}></button>
            <button onClick={upload}></button>
            <table>
                <tbody>
            {userName.map( (UserPrint, i )=>
            {
                const previousItem = i > 0  ? userName[i-1] : null;
                console.log(i);
                if((i > 0 && previousItem.ymd != UserPrint.ymd) || i == 0)
                return <tr key={i}><td colSpan={2}> <div>--------------- {UserPrint.ymd}-------------------</div></td></tr>;
                // if(befDate != UserPrint.ymd){
                //     SetBefDate(UserPrint.ymd);
                //     return <div>----------------</div>;
                // }
                return   PrintChats(UserPrint);
            })}
            </tbody>
            </table>
        </div>
    )
    function PrintChats(UserPrint){
        return (
            <tr key={UserPrint.key}>
                <td>
                    <div className='chat-name'> {UserPrint.name} </div>
                </td>
                <td>
                    <div className='speech-bubble'> {UserPrint.text}</div>
                </td>
            </tr>
        )
    }
}
export default Chat;